body {
  background: #f3f3f3;
  margin: 0;
  max-width: 1400px;
  width: 80%;
  height: 600px;
  padding: 0;
  padding: 10px;
}

.container {
  width: 100%;
  display: grid;
  grid-template-columns: 0.3fr 1fr;
  grid-gap: 10px;
}

.container .filter {
  box-shadow: 0 0px 1px rgba(0, 0, 0, 0.2);
  padding: 10px;
  margin: 10px auto;
  background: white;
  color: #424242;
}

.container .result {
  margin: 10px 20px;
}

.placeholder {
  border: 1px dashed #424242;
  padding: 10px;
  margin: 10px auto;
  background: white;
}

@media (max-width: 768px) {
  .container {
    grid-template-columns: auto;
  }
  .container .result {
    margin: 10px 0px;
  }
}


